(function () {
    'use strict';
    angular.module('paperless').controller('NotificationAddController', NotificationAddController);
    /** @ngInject */
    function NotificationAddController($timeout, ProfileSvc, FileUploader, $location, http, SiteSvc,Notification,NotificationSvc) {
        var vm = this;
        vm.defaultList = [];
        vm.iserror = false;
        vm.notification = NotificationSvc.currentItem;
        /*
        var toolbarOptions = [
        ['bold','italic', 'underline', 'strike'],        // toggled buttons
        ['blockquote', 'code-block'],           // custom button values
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
    // superscript/subscript
        [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
        [{ 'direction': 'rtl' }],                         // text direction // custom dropdown
        [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
        [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
        [{ 'font': [] }],
        [{ 'align': [] }],
        [ 'link', 'image', 'video', 'formula' ],
        ['clean']                                         // remove formatting button
        ];
        */
        var toolbarOptions = [];
        
        var quill = new Quill('#notificationeditor #editor', {
            modules: {
                toolbar: toolbarOptions
                        },
            theme: 'snow'
        });
        vm.DocumentTypeList = NotificationSvc.DocumentTypeList;
        vm.DocumentTypeList_In_Use = [];
        vm.DocumentTypeList_Not_In_Use = NotificationSvc.DocumentTypes.Not_In_Use;
        if (vm.notification.mode == "edit") {
            if (vm.notification.DocumentTypes.length > 0) {
                var tmp_document_types = [];
                for (var i = 0; i < vm.notification.DocumentTypes.length; i++) {
                    for (var j = 0; j < vm.DocumentTypeList.length; j++) {
                        if (vm.notification.DocumentTypes[i].DocumentTypeId == vm.DocumentTypeList[j].DocumentTypeId) {
                            var tmp_doc_type = {};
                            tmp_doc_type = vm.DocumentTypeList[j];
                            tmp_document_types.push(tmp_doc_type);
                        }
                    }
                }
                vm.DocumentTypeList_In_Use = tmp_document_types;
            }
            $timeout(function () {
               $("#notificationeditor #editor")[0].firstChild.innerHTML = vm.notification.EmailMessageHtmlContent;
            });
        }
        
        
        vm.saveNotification = function () {
            var NotificationObj = {};
            NotificationObj = vm.notification;
            NotificationObj.EmailMessageHtmlContent = $("#notificationeditor #editor")[0].firstChild.innerHTML;
            var tmp_document_type_list = [];
            for (var i = 0; i < vm.DocumentTypeList_In_Use.length; i++) {
                var tmpType = {};
                tmpType.DocumentTypeId = vm.DocumentTypeList_In_Use[i].DocumentTypeId;
                tmpType.DocumentGeneratedByCode = vm.DocumentTypeList_In_Use[i].DocumentGeneratedByCode;
                tmp_document_type_list.push(tmpType);
            }
            NotificationObj.DocumentTypes = tmp_document_type_list;
            http.put('notification/' + SiteSvc.CurrentItem.ClientId , NotificationObj).then(function (response) {
                Notification.success('Your Notification change has been saved and published successfully.');
            }, function (error) {
                vm.isloading = false;
            });
        }
        

        
        vm.testmails = [''];
          
        vm.add = function(){
            var tempMail =  ''
            vm.testmails.push(tempMail);
        }
        
        
        vm.delete = function(index){
            vm.testmails.splice(index, 1);
        }
    }
})();
