(function () {
    'use strict';
    angular.module('paperless').controller('SitesAddController', SitesAddController);
    /** @ngInject */
    function SitesAddController($http, $timeout, ProfileSvc, $location) {
        var vm = this;
        vm.additem = false;
        getSites();

        function getSites() {
            // firebase.database().ref('sites').once('value').then((data) => {
            firebase.database().ref('sites').once('value').then(function (data) {
                console.log("this is webservice call" + data.val());
                //vm.defaultList = data.val();
                $timeout(function () {
                    update(data);
                });
            });
        }
        $(document).ready(function () {
            // the "href" attribute of .modal-trigger must specify the modal ID that wants to be triggered
            $('.modal').modal();
        });
        $('#sitesadd').modal('open');
        function update(data) {
            vm.defaultList = data.val();
            vm.defaultListTmp = vm.defaultList;
        }
        vm.firstLetter = function (letter) {
            var letter = letter.slice(0, 1);
            return letter;
        }
        vm.selectSite = function (item) {
            if (item.isSelected) {}
            else {
                for (var i = 0; i < vm.defaultList.length; i++) {
                    vm.defaultList[i].isSelected = false;
                }
                item.isSelected = true;
                ProfileSvc.profile.company = item.clientName;
                ProfileSvc.profile.selectedLink = item.link;
            }
            var tmp = angular.fromJson(angular.toJson(vm.defaultList));
            firebase.database().ref('sites').set(tmp);
            $location.path('/configuration/theme');
        }
        vm.initsite = function (item) {
            vm.additem = true;
            vm.currentItem = item;
        }
        vm.close = function () {
            //$location.path('/sites');
            vm.additem = false;
            $('#sitesadd').modal('close');
        }
        vm.generateSite = function () {
            vm.currentItem.link = vm.uniqueString;
            console.log(vm.defaultList);
            var tmp = angular.fromJson(angular.toJson(vm.defaultList));
            me.profile.company = vm.currentItem.clientName;
            me.profile.selectedLink = vm.currentItem.link;
            firebase.database().ref('sites').set(tmp);
            vm.additem = false;
            $location.path('/configuration/theme');
        }
    }
})();
