(function () {
    'use strict';
    angular.module('paperless').controller('WelcomeController', WelcomeController);
    /** @ngInject */
    function WelcomeController($timeout, SettingSvc, http,WelcomeSvc,$location,SiteSvc) {
        var vm = this;
        vm.defaultList = [];
        vm.name = '';
        vm.isloading = true;
        vm.additem = false;
        vm.isshowwelcome = true;
    
        vm.save = function (item) {
           
        }
        
        vm.publish = function(item){
            vm.isloading = true;
            var tmpObj = {};
            tmpObj.WelcomeCode = item.WelcomeCode;
            tmpObj.IsPublished = true;
            
            http.put('site/' + SiteSvc.CurrentItem.ClientId + '/welcome/published',tmpObj).then(function (response) {
                
                item.IsActivePublished = true;
                updateStatus(item);
                vm.isloading = false;
            }, function (error) {
                vm.isloading = false;
            });
        }
        
        
        function updateStatus(item){
            for(var i = 0; i < vm.defaultList.length; i++){
                if(vm.defaultList[i].WelcomeCode !== item.WelcomeCode){
                    
                    vm.defaultList[i].IsActivePublished = false;
                    
                }
            }
        }
        
        vm.edit = function(item)
        {
             WelcomeSvc.currentItem = item;
             $location.path('/configuration/welcome/add');
        }
        
        getWelcome();

        function getWelcome() {
            http.get('site/' + SiteSvc.CurrentItem.ClientId + '/welcome').then(function (response) {
                $timeout(function () {
                    update(response.data);
                });
            }, function (error) {
                vm.isloading = false;
            });
        }

        function update(data) {
            vm.defaultList = data;
          
            if (vm.defaultList == null) {
                vm.isshowwelcome = true;
            }
            else {
                vm.isshowwelcome = false;
            }
            vm.isloading = false;
        }
    }
})();
(function () {
    'use strict';
    angular.module('paperless').controller('Topbar_Controller', Topbar_Controller).service('ProfileSvc', function () {
        var svc = this;
        svc.draftItem = {}
        svc.changeSetting = function (clientID) {}
        svc.user = {}
        svc.sites = {
            defaultList: []
        };
        svc.support = {};
    });
    /** @ngInject */
    function Topbar_Controller($timeout, ProfileSvc, $location, me,  http, CurrentClient, SiteList, SiteSvc, ThemeSvc, BaseUrl,Notification,$filter) {
        var vm = this;
        vm.profile = me;
        vm.shownlist = false;
        vm.focusinput = false;
        vm.searchObj = {};
        vm.istoggle = false;
        vm.show_site_add = false;
        vm.filterTxt = {
            'ClientName': ''
            , 'ClientId': ''
        };
        vm.client_generate = '';
        SiteSvc.CurrentItem = CurrentClient;
        vm.searchObj = jQuery.extend(true, {}, CurrentClient);
        checkStatus(vm.searchObj);
        getSupport(vm.searchObj);
        goSite(vm.searchObj);
        vm.client_to_generate = jQuery.extend(true, {}, vm.searchObj);
        vm.baseUrl = BaseUrl.Url;
        ProfileSvc.sites.defaultList = SiteList.Clients;
        vm.sites = ProfileSvc.sites;
        vm.userName = me.firstname + '  ' + me.lastname;
        vm.toggle = function () {
            if (vm.istoggle) {
                $('.static-sidebar-wrapper').css("width", "248px");
            }
            else {
                $('.static-sidebar-wrapper').css("width", "0px");
             
            }
            vm.istoggle = !vm.istoggle;
        }
        vm.goClient = function (item) {
            vm.shownlist = false;
            vm.verifyMsg = null;
            if (item.SiteStatus !== null) {
                vm.searchObj = jQuery.extend(true, {}, item);
                checkStatus(vm.searchObj);
                getSupport(vm.searchObj);
                //ProfileSvc.draftItem = item;
                goSite(item);
            }
            else {
                vm.uniqueString = '';
                vm.searchObj = jQuery.extend(true, {}, item);
                checkStatus(vm.searchObj);
                vm.client_to_generate = jQuery.extend(true, {}, vm.searchObj);
                vm.show_site_add = true;
            }
        }
        vm.getFocusinput = function () {
            vm.focusinput = true;
            vm.filterTxt = {
                'ClientName': ''
            };
        }
        vm.changeSearch = function () {
            var tmp_search = jQuery.extend(true, {}, vm.searchObj);
            vm.filterTxt = vm.searchObj.ClientName;
            //vm.filterTxt.ClientId = tmp_search.ClientId;
        }
        vm.close = function () {
            vm.searchObj = jQuery.extend(true, {}, ProfileSvc.draftItem);
            $('#sitesadd').modal('close');
        }
        vm.changeurl = function () {
            vm.verifyMsg = null;
        }
        vm.generateSite = function () {
            if (vm.uniqueString !== '') {
                http.post('site/' + vm.client_to_generate.ClientId + '/urlPath', {
                    "UrlPath": vm.uniqueString
                }).then(function (success) {
                    if (!success.data.Success) {
                        vm.verifyMsg = success.data.Message;
                        return;
                    }
                    else {
                        http.post( 'site/' + vm.client_to_generate.ClientId, {
                            "UrlPath": vm.uniqueString
                        }).then(function (success) {
                            SiteSvc.CurrentItem = vm.client_to_generate;
                            getSites();
                            Notification.success('Your site has been created successfully.');
                        }, function (error) {
                            Notification.error('Sorry, something went wrong, Please try again later.');
                            vm.verifyMsg = error.data.Message;;
                            return;
                        })
                    }
                }, function (error) {
                    vm.verifyMsg = error.data.Message;
                    return;
                })
            }
        }
        vm.changeStatus = function (client) {
     
           
             http.put('site/' + SiteSvc.CurrentItem.ClientId + '/client/published', {
                    "IsPublished": client.isPublished
                }).then(function (success) {
                 if(client.isPublished){
                        vm.searchObj.SiteStatus = 'Published';
                        Notification.success('Your site has been published successfully.'); 
                 }else{
                        vm.searchObj.SiteStatus = 'Un-Published';
                        Notification.success('Your site has been un-published successfully.');
                 }
                for (var i = 0; i < ProfileSvc.sites.defaultList.length; i++) {
                if (ProfileSvc.sites.defaultList[i].ClientId == SiteSvc.CurrentItem.ClientId) {
                   ProfileSvc.sites.defaultList[i].SiteStatus = vm.searchObj.SiteStatus;
                  
                }
            }
                 
                }, function (error) {
                    Notification.error('Sorry, something went wrong, Please try again later.');
                    return;
                })
            
        }
        
        vm.check_name = function(){
            console.log(vm.searchObj);
           var abc =  $filter('filter')(vm.sites.defaultList, vm.searchObj);
             console.log(abc);
            console.log(vm.sites.defaultList.length);
        }

        function getSupport(client) {
            ProfileSvc.support.ClientServiceContactName = client.ClientServiceContactName;
            ProfileSvc.support.ClientServiceContactEmail = client.ClientServiceContactEmail;
            ProfileSvc.support.ClientServiceContactPhone = client.ClientServiceContactPhone;
            ProfileSvc.support.ProfilePictureUrl = client.ProfilePictureUrl;
        }

        function goSite(item) {
            SiteSvc.CurrentItem = jQuery.extend(true, {}, item);
            if (item.SiteStatus == null) {
                vm.show_site_add = true;
            }
            else 
            {   vm.show_site_add = false;
                ThemeSvc.getTheme();
                
                if (item.SiteStatus == "Un-Published") {
                   $location.path('/appearance');
                }
                else {
                    $location.path('/dashboard');              
                }
            }
        }

        function checkStatus(client) {
            if (client.SiteStatus == null) {
                vm.show_site_add = true;
                client.isPublished = false;
            }
            else if (client.SiteStatus == "Un-Published") {
                client.isPublished = false;
            }
            else {
                client.isPublished = true;
            }
        }

        function getSites() {
            http.get('user/' + me.userid + '?' + me.groups).then(function (response) {
                $timeout(function () {
                    update(response.data);
                });
            }, function (error) {});
        }

        function update(data) {
            SiteList = data.Clients;
            ProfileSvc.sites.defaultList = data.Clients;
            vm.sites = ProfileSvc.sites;
            for (var i = 0; i < ProfileSvc.sites.defaultList.length; i++) {
                if (ProfileSvc.sites.defaultList[i].ClientId == SiteSvc.CurrentItem.ClientId) {
                    SiteSvc.CurrentItem = ProfileSvc.sites.defaultList[i];
                    vm.searchObj = jQuery.extend(true, {}, SiteSvc.CurrentItem);
                    checkStatus(vm.searchObj);
                    getSupport(vm.searchObj);
                    if (ProfileSvc.sites.defaultList[i].SiteStatus == null) {
                        vm.show_site_add = true;
                        break;
                    }
                    else if (ProfileSvc.sites.defaultList[i].SiteStatus == "Un-Published") {
                        ThemeSvc.getTheme();
                        vm.show_site_add = false;
                        $location.path('/configuration/theme');
                        break;
                       
                    }
                    else {
                        $location.path('/dashboard');
                        vm.show_site_add = false;
                        break;
                    }
                }
            }
        }
    }
})();
(function () {
    'use strict';
    angular.module('paperless').controller('ThemeController', ThemeController).service('ThemeSvc', function ($location, http, SiteSvc) {
        var me = this;
        me.siteBranding = {
            colors: {
                primary: {}
                , second: {}
                , thrid: {}
            }
            , thumbtmp: null
            , url: null
            , contact: {
                address: {}
                , phones: {}
            }
            , social: {
                facebook: {
                    "SocialMediaCode": "Facebook"
                    , "SocialMediaUrl": ""
                }
                , google: {
                    "SocialMediaCode": "Google+"
                    , "SocialMediaUrl": ""
                }
                , linkedin: {
                    "SocialMediaCode": "LinkedIn"
                    , "SocialMediaUrl": ""
                }
                , twitter: {
                    "SocialMediaCode": "Twitter"
                    , "SocialMediaUrl": ""
                }
                , youtube: {
                    "SocialMediaCode": "YouTube"
                    , "SocialMediaUrl": ""
                }
            }
        }
        me.notify_change = function(){
            
        }
        me.loadingStatus = {
            isloading: false
        }
        me.goTheme = function () {
            $location.path('/appearance');
        }
        me.getTheme = function () {
            me.loadingStatus.isloading = true;
            me.siteBranding.url = SiteSvc.CurrentItem.SiteUrl;
            http.get('site/' + SiteSvc.CurrentItem.ClientId).then(function (response) {
                update(response.data);
                renderClr();
                me.loadingStatus.isloading = false;
            }, function (error) {
                renderClr();
                me.loadingStatus.isloading = false;
            });
        }

        function update(data) {
            var theme = data;
            if (theme !== null) {
                if (theme.Branding.Colors !== null) {
                    for (var i = 0; i < theme.Branding.Colors.length; i++) {
                        if (theme.Branding.Colors[i].ColorCode == 'PrimaryBackground') {
                            me.siteBranding.colors.primary = theme.Branding.Colors[i].Color;
                        }
                        else if (theme.Branding.Colors[i].ColorCode == 'PrimaryAction') {
                            me.siteBranding.colors.second = theme.Branding.Colors[i].Color;
                        }
                        else if (theme.Branding.Colors[i].ColorCode == 'ActiveState') {
                            me.siteBranding.colors.third = theme.Branding.Colors[i].Color;
                        }
                    }
                }
                if (theme.Branding.Images !== null) {
                    for (var i = 0; i < theme.Branding.Images.length; i++) {
                        me.siteBranding.thumbtmp = theme.Branding.Images[i].ImageEncoded;
                    }
                }
                if (theme.ContactInfo.Addresses !== null) {
                    for (var i = 0; i < theme.ContactInfo.Addresses.length; i++) {
                        me.siteBranding.contact.address = theme.ContactInfo.Addresses[0];
                    }
                }
                if (theme.ContactInfo.Phones !== null) {
                    for (var i = 0; i < theme.ContactInfo.Phones.length; i++) {
                        if (theme.ContactInfo.Phones[i].PhoneCode == "Telephone 1") {
                            me.siteBranding.contact.phones.phone1 = theme.ContactInfo.Phones[i].PhoneNumber;
                        }
                        else if (theme.ContactInfo.Phones[i].PhoneCode == "Telephone 2") {
                            me.siteBranding.contact.phones.phone2 = theme.ContactInfo.Phones[i].PhoneNumber;
                        }
                        else if (theme.ContactInfo.Phones[i].PhoneCode == "Fax") {
                            me.siteBranding.contact.phones.fax = theme.ContactInfo.Phones[i].PhoneNumber;
                        }
                    }
                }
                if (theme.ContactInfo.SocialMedia !== null) {
                    me.siteBranding.social.facebook = $.grep(theme.ContactInfo.SocialMedia, function (content) {
                        return content.SocialMediaCode === 'Facebook';
                    })[0];
                    me.siteBranding.social.google = $.grep(theme.ContactInfo.SocialMedia, function (content) {
                        return content.SocialMediaCode === 'Google+';
                    })[0];
                    me.siteBranding.social.linkedin = $.grep(theme.ContactInfo.SocialMedia, function (content) {
                        return content.SocialMediaCode === 'LinkedIn';
                    })[0];
                    me.siteBranding.social.twitter = $.grep(theme.ContactInfo.SocialMedia, function (content) {
                        return content.SocialMediaCode === 'Twitter';
                    })[0];
                    me.siteBranding.social.youtube = $.grep(theme.ContactInfo.SocialMedia, function (content) {
                        return content.SocialMediaCode === 'YouTube';
                    })[0];
                }
            }
        }

        function renderClr() {
            $(".primarycolorpicker").spectrum({
                color: me.siteBranding.colors.primary
                , preferredFormat: "hex"
                , showInput: true
                , change: function (color) {
                    me.siteBranding.colors.primary = null;
                    me.siteBranding.colors.primary = color.toHexString();
                    me.notify_change();
                }
            });
            $(".secondcolorpicker").spectrum({
                color: me.siteBranding.colors.second
                , preferredFormat: "hex"
                , showInput: true
                , change: function (color) {
                    me.siteBranding.colors.second = {};
                    me.siteBranding.colors.second = color.toHexString();
                    me.notify_change();
                }
            });
            $(".thirdcolorpicker").spectrum({
                color: me.siteBranding.colors.third
                , preferredFormat: "hex"
                , showInput: true
                , change: function (color) {
                    me.siteBranding.colors.third = {};
                    me.siteBranding.colors.third = color.toHexString();
                    me.notify_change();
                }
            });
        }
    });
    /** @ngInject */
    function ThemeController(http, Notification, ThemeSvc, SiteSvc) {
        var vm = this;
        ThemeSvc.getTheme();
        vm.ThemeSvc = ThemeSvc;
        vm.colors = ThemeSvc.siteBranding.colors;
        ThemeSvc.notify_change = function(){ 
            console.log(vm.colors);
        }
        vm.loadingStatus = ThemeSvc.loadingStatus;
        vm.save = function () {
            var logo;
            var obj = {};
            obj.Branding = {};
            obj.Branding.BrandCode = "Default";
            obj.Branding.IsDefault = true;
            obj.Branding.Images = [];
            var tempImg = {};
            tempImg.ImageCode = 'logo';
            tempImg.ImageEncoded = vm.siteBranding.thumbtmp;
            obj.Branding.Images.push(tempImg);
            obj.Branding.Colors = [];
            var primarycolor = {};
            primarycolor.ColorCode = "PrimaryBackground";
            primarycolor.Color = ThemeSvc.siteBranding.colors.primary;
            var secondarycolor = {};
            secondarycolor.ColorCode = "PrimaryAction";
            secondarycolor.Color = ThemeSvc.siteBranding.colors.second;
            var thridcolor = {};
            thridcolor.ColorCode = "ActiveState";
            thridcolor.Color = ThemeSvc.siteBranding.colors.third;
            obj.Branding.Colors.push(primarycolor);
            obj.Branding.Colors.push(secondarycolor);
            obj.Branding.Colors.push(thridcolor);
            obj.Branding.IsPublished = true;
            obj.ContactInfo = {};
            obj.ContactInfo.IsPublished = true;
            obj.ContactInfo.ContactCode = "Default";
            obj.ContactInfo.Addresses = [];
            var address = {};
            address.AddressCode = "Default";
            address.Address1 = vm.siteBranding.contact.address.Address1;
            address.Address2 = vm.siteBranding.contact.address.Address2;
            address.City = "";
            address.State = "";
            address.Zip5 = "";
            address.Zip4 = "";
            address.Attention = vm.siteBranding.contact.address.Attention;
            obj.ContactInfo.Addresses.push(address);
            obj.ContactInfo.HoursOfOperation = "";
            obj.ContactInfo.Phones = [];
            var phone1 = {};
            phone1.PhoneCode = "Telephone 1";
            phone1.PhoneNumber = vm.siteBranding.contact.phones.phone1;
            obj.ContactInfo.Phones.push(phone1);
            var phone2 = {};
            phone2.PhoneCode = "Telephone 2";
            phone2.PhoneNumber = vm.siteBranding.contact.phones.phone2;
            obj.ContactInfo.Phones.push(phone2);
            var fax = {};
            fax.PhoneCode = "Fax";
            fax.PhoneNumber = vm.siteBranding.contact.phones.fax;
            obj.ContactInfo.Phones.push(fax);
            obj.ContactInfo.SocialMedia = [];
            var facebook = {};
            //vm.social.facebook.SocialMediaCode = "Facebook";
            facebook = vm.siteBranding.social.facebook;
            var google = {};
            //vm.social.google.SocialMediaCode = "Google+";
            google = vm.siteBranding.social.google;
            var linkedin = {};
            //vm.social.linkedin.SocialMediaCode = "LinkedIn";
            linkedin = vm.siteBranding.social.linkedin;
            var twitter = {};
            //vm.social.twiiter.SocialMediaCode = "Twitter";
            twitter = vm.siteBranding.social.twitter;
            var youtube = {};
            //vm.social.youtube.SocialMediaCode = "YouTube";
            youtube = vm.siteBranding.social.youtube;
            obj.ContactInfo.SocialMedia.push(facebook);
            obj.ContactInfo.SocialMedia.push(google);
            obj.ContactInfo.SocialMedia.push(linkedin);
            obj.ContactInfo.SocialMedia.push(twitter);
            obj.ContactInfo.SocialMedia.push(youtube);
            http.put('site/' + SiteSvc.CurrentItem.ClientId, obj).then(function (response) {
                Notification.success('Your changes have been saved and published successfully.');
            }, function (response) {
                Notification.error('Sorry, something went wrong, Please try again later.');
            });
        }
    }
})();
(function () {
    'use strict';
    angular.module('paperless').controller('SupportController', SupportController);
    /** @ngInject */
    function SupportController($timeout, SettingSvc,  http, SiteSvc, Notification,ThemeSvc) {
        var vm = this;
        vm.faqs = [];
        vm.theme = ThemeSvc.siteBranding;
        vm.isloading = true;
        vm.originfaqs = [];
        vm.toBeDelete = {};
         var toolbarOptions = [
        ['bold','italic', 'underline', 'strike'],        // toggled buttons
        ['blockquote', 'code-block'],           // custom button values
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
    // superscript/subscript
        [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
        [{ 'direction': 'rtl' }],                         // text direction
        [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
        [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
        [{ 'font': [] }],
        [{ 'align': [] }],
        [ 'link', 'image', 'video', 'formula' ],
        ['clean']                                         // remove formatting button
        ];
        
        var quill = new Quill('#supporteditor #editor', {
            modules: {
                toolbar: toolbarOptions
            }
            , theme: 'snow'
        });
        
        vm.add = function () {
        
            for (var i = 0; i < vm.faqs.length; i++) {
             vm.faqs[i].isshow = true;
            }
            
            var newItem = {
                "Question": "Please give your question here..."
                , "Answer": "<p>Please give your answer here...</p>"
            }
            newItem.isshow = false;
            newItem.FaqUid = null;
            vm.faqs.push(newItem);
            $timeout(function () {
                $("#supporteditor #editor")[0].firstChild.innerHTML = newItem.Answer;
                $('#supporteditor').detach().appendTo('#faq' + (vm.faqs.length - 1));
                document.getElementById('editor').scrollIntoView();
            });
        }
        vm.cancel = function (index) {
            //$('#editorpanel').detach().appendTo('#supporteditor');
            $('#supporteditor').detach().appendTo('#editorpanel');
            var temp = jQuery.extend(true, [], vm.originfaqs);
            vm.faqs = temp;      
        }
        vm.save = function (index) {
            if (vm.faqs[index].FaqUid == null) {
                var saveObj = {};
                saveObj.Question = vm.faqs[index].Question;
                saveObj.Answer = $("#supporteditor #editor")[0].firstChild.innerHTML;
                saveObj.ClientId = SiteSvc.CurrentItem.ClientId;
                http.put('site/' + SiteSvc.CurrentItem.ClientId + '/faq', saveObj).then(function (response) {
                    $timeout(function () {
                        vm.faqs[index].Answer = $("#supporteditor #editor")[0].firstChild.innerHTML;
                        vm.faqs[index].isshow = true;
                        vm.faqs[index].FaqUid = response.data.Id;
                        vm.originfaqs = jQuery.extend(true, [], vm.faqs);
                    });
                    Notification.success('Your FAQ has been saved successfully.');
                }, function (error) {
                    vm.isloading = false;
                    Notification.error('Sorry, something went wrong, Please try again later.');
                });
            }
            else {
                var updateObj = {};
                updateObj.FaqUid = vm.faqs[index].FaqUid;
                updateObj.Question = vm.faqs[index].Question;
                updateObj.Answer = $("#supporteditor #editor")[0].firstChild.innerHTML;
                updateObj.ClientId = SiteSvc.CurrentItem.ClientId;
                http.put( 'site/' + SiteSvc.CurrentItem.ClientId + '/faq', updateObj).then(function (response) {
                    $timeout(function () {
                        vm.faqs[index].Answer = $("#supporteditor #editor")[0].firstChild.innerHTML;
                        vm.faqs[index].isshow = true;
                        vm.faqs[index].FaqUid = response.data.Id;
                        vm.originfaqs = jQuery.extend(true, [], vm.faqs);
                    });
                    Notification.success('Your FAQ has been saved successfully.');
                }, function (error) {
                    vm.isloading = false;
                    Notification.error('Sorry, something went wrong, Please try again later.');
                });
            }
        }
        vm.edit = function (index) {
            for (var i = 0; i < vm.faqs.length; i++) {
                if (i == index) {
                    vm.faqs[index].isshow = false;
                }
                else {
                    vm.faqs[i].isshow = true;
                }
            }
            $timeout(function () {
                $("#supporteditor #editor")[0].firstChild.innerHTML = vm.faqs[index].Answer;
                $('#supporteditor').detach().appendTo('#faq' + index);
            });
        }
        vm.delete = function (index) {
            vm.toBeDelete = vm.faqs[index];
            $('#confirmmodal').modal('open');
        }
        
            $(document).ready(function(){
            $('#confirmmodal').modal();
            });
        
        vm.confirmdelete = function () {
            var deleteObj = {};
            deleteObj.Uid = vm.toBeDelete.FaqUid;
            http.put('site/' + SiteSvc.CurrentItem.ClientId + '/faq/delete', deleteObj).then(function (response) {
                $timeout(function () {
                    Notification.success('Your FAQ has been deleted successfully.');
                    getFAQ();
                    $('#confirmmodal').modal('close');
                });
            }, function (error) {
                vm.isloading = false;
                Notification.error('Sorry, something went wrong, Please try again later.');
            });
        }
     
        getFAQ();

        function getFAQ() {
            http.get('site/' + SiteSvc.CurrentItem.ClientId + '/faq').then(function (response) {
                $timeout(function () {
                    update(response.data);
                });
            }, function (error) {
                vm.isloading = false;
                Notification.error('Sorry, something went wrong, Please try again later.');
            });
        }
        vm.downorder = function (currentitem, nextitem) {
            var oidList = [];
            
            for(var i = 0; i < vm.faqs.length; i ++){
                oidList.push(vm.faqs[i].FaqUid)
            }
           
            var temp = {};
            temp = oidList[currentitem];
            oidList[currentitem]= oidList[nextitem];
            oidList[nextitem] = temp;
            
        
            var orderObj = {};
            orderObj.FaqUids = oidList;
            orderObj.ClientId =  SiteSvc.CurrentItem.ClientId;          
            http.put('site/' + SiteSvc.CurrentItem.ClientId + '/faq/order',orderObj).then(function (response) {
                Notification.success('Your FAQ order has been changed successfully.');
                $timeout(function () {
                    getFAQ();
                });
            }, function (error) {
                vm.isloading = false;
                Notification.error('Sorry, something went wrong, Please try again later.');
            });
        }
        vm.uporder = function (currentitem, preitem) {
            var oidList = [];
               
            for(var i = 0; i < vm.faqs.length; i ++){
                oidList.push(vm.faqs[i].FaqUid)
            }
         
            var temp = {};
            temp = oidList[currentitem];
            oidList[currentitem] = oidList[preitem];
            oidList[preitem] = temp;

         
            var orderObj = {};
            orderObj.FaqUids = oidList;
            orderObj.ClientId =  SiteSvc.CurrentItem.ClientId;          
            http.put('site/' + SiteSvc.CurrentItem.ClientId + '/faq/order',orderObj).then(function (response) {
                Notification.success('Your FAQ order has been changed successfully.');
                $timeout(function () {
                    getFAQ();
                });
            }, function (error) {
                vm.isloading = false;
                Notification.error('Sorry, something went wrong, Please try again later.');
            });
            
        }
        vm.close = function () {
            $('.confirmmodal').modal('close');
        }

        function update(data) {
            vm.faqs = data;
            for (var i = 0; i < vm.faqs.length; i++) {
                vm.faqs[i].isshow = true;
            }
            vm.originfaqs = jQuery.extend(true, [], vm.faqs);
            vm.isloading = false;
        }
    }
})();
(function () {
    'use strict';
    angular.module('paperless').controller('SitesController', SitesController).service('SiteSvc', function () {
        var me = this;       
         me.CurrentItem = {};
    });
    /** @ngInject */
    function SitesController(http, $timeout, ProfileSvc, $location, me, Pagination,ThemeSvc,SiteSvc) {
        var vm = this;
        vm.additem = false;
        vm.isloading = true;
        vm.currentItem = {};
        vm.pagination = Pagination.getNew(50);
        vm.pagination.perPage = 50;
        vm.sgstr = '';
        if (Array.isArray(me.securitygroups)) {
            vm.sgstr = 'sg=';
            for (var i = 0; i < me.securitygroups.length; i++) {
                vm.sgstr = vm.sgstr + me.securitygroups[i] + '&';
            }
        }
        else {
            vm.sgstr = 'sg=' + me.securitygroups;
        }
        getSites();

        function getSites() {
            http.get('user/' + me.userid + '?' + vm.sgstr).then(function (response) {
                $timeout(function () {
                    update(response.data);
                });
            }, function (error) {
                
                  vm.isloading = false;
            });
        }

        function update(data) {
            vm.defaultList = data.Clients;
            vm.defaultListTmp = vm.defaultList;
            vm.pagination.numPages = Math.ceil(vm.defaultList.length / vm.pagination.perPage);
            vm.isloading = false;
        }
        vm.selectSite = function (item) {
               
                //ProfileSvc.profile.company = item.ClientName;
                //ProfileSvc.profile.selectedLink = item.SiteUrl;
                ProfileSvc.topsearch.ClientName = item.ClientName;
            SiteSvc.CurrentItem.ClientId = item.ClientId;
            SiteSvc.CurrentItem.ClientName = item.ClientName;
            var tmp = angular.fromJson(angular.toJson(vm.defaultList));
            ThemeSvc.getTheme();
           // $location.path('/configuration/theme');
        }
        vm.changeStatus = function(item){
            if(item.SiteStatus == 'Un-Published'){
                item.SiteStatus = 'Published';
            }else if(item.SiteStatus == 'Published'){
                item.SiteStatus = 'Un-Published';
            }
        }
        
        vm.initsite = function (item) {
            if (item.SiteUrl == null) {
                ProfileSvc.draftItem = item;
                
                $('#sitesadd').modal({
                    dismissible: false
                    , complete: function () {
                            getSites();
                        } // Callback for Modal close
                });
                $('#sitesadd').modal('open');
            }
        }
    }
})();
