(function () {
    'use strict';
    angular.module('paperless').controller('Left_Menu_Controller', Left_Menu_Controller);
    /** @ngInject */
    function Left_Menu_Controller(ProfileSvc, $location, ContentSvc) {
        var vm = this;
        vm.support = ProfileSvc.support;
        vm.parent_expand = function (event) {
            if (event.currentTarget.className === "hasChild open") {
                event.currentTarget.className = "hasChild";
            }
            else {
                event.currentTarget.className = "hasChild open";
            }
        }

        vm.go_dashboard = function () {
          $location.path('dashboard');
        }

        vm.go_mymenu = function(){
          $location.path('/menu');
        }
        
        vm.go_mystore = function(){
          $location.path('/store');
        }
        vm.go_orders = function(){
           $location.path('/orders'); 
        }
        vm.go_access = function(){
            $location.path('/access');
        }
        vm.go_loyalty = function(){
            $location.path('/loyalty');
        }
        vm.go_customer = function(){
           $location.path('/customer'); 
        }

        vm.child_expand = function (event, menu) {
            ContentSvc.change_status.path = menu;
            ContentSvc.change_status.target = event;
            if (ContentSvc.change_status.ischanged) {
                $("#content_nav_Modal").modal();
            }
            else {
                clear_status();
                event.currentTarget.parentElement.parentElement.className = "hasChild open active";
                event.currentTarget.className = "active";
                $location.path(menu);
            }
        }
        vm.leave_page = function () {
            clear_status();
            $("#content_nav_Modal").modal('hide');
            ContentSvc.change_status.target.currentTarget.parentElement.parentElement.className = "hasChild open active";
            ContentSvc.change_status.target.currentTarget.className = "active";
            $location.path(ContentSvc.change_status.path);
            ContentSvc.change_status = {
                ischanged: false
                , path: null
                , target: null
            }
        }

        function clear_status() {
            var list = angular.element('ul.acc-menu.parent > li');
            angular.forEach(list, function (perent, key) {
                $(perent).removeClass("active");
            });
            var children = angular.element("ul.acc-menu.child > li");
            angular.forEach(children, function (child, key) {
                child.className = "";
            })
        }
        /*
        vm.open_menu = function (event, level) {

            $('body').on('click', 'ul.acc-menu a', function() {
                var LIs = $('body').closest('ul.acc-menu').children('li');

                if (event.currentTarget.parents('ul.acc-menu').length > 1) {
                    if(event.currentTarget.siblings('ul.acc-menu:visible').length>0)
                       event.currentTarget.closest('li').removeClass('open');
                    else
                        event.currentTarget.closest('li').addClass('open');
                        event.currentTarget.siblings('ul.acc-menu').slideToggle({
                            duration: 200
                        });
                }
            });

            //var LIs = event.currentTarget.closest('ul.acc-menu').children('li');

        }*/
    }
})();

