(function () {
    'use strict';
    angular.module('paperless').controller('AgreementController', AgreementController);
    /** @ngInject */
    function AgreementController($timeout, SettingSvc, http,AgreementSvc,$location,SiteSvc) {
        var vm = this;
        vm.additem = false;
        vm.defaultList = [];
        vm.isloading = false;
        getAgreement();

        function getAgreement() {
             vm.isloading = true;
            http.get( 'site/' + SiteSvc.CurrentItem.ClientId + '/siteterms').then(function (response) {
                $timeout(function () {
                    update(response.data);
                });
            }, function (error) {
                vm.isloading = false;
            });
        }

        function update(data) {
            vm.defaultList = data;
             vm.isloading = false;
        }
        
         vm.edit = function(item)
        {
             AgreementSvc.currentItem = item;
             $location.path('/configuration/agreement/add');
        }
        
         vm.publish = function(item){
            vm.isloading = true;
            var tmpObj = {};
            tmpObj.TermsCode = item.TermsCode;
            tmpObj.IsPublished = true;
            
            http.put( 'site/' + SiteSvc.CurrentItem.ClientId + '/siteterms/published',tmpObj).then(function (response) {
                
                item.IsActivePublished = true;
                updateStatus(item);
                vm.isloading = false;
            }, function (error) {
                vm.isloading = false;
            });
        }
         
           function updateStatus(item){
            for(var i = 0; i < vm.defaultList.length; i++){
                if(vm.defaultList[i].TermsCode !== item.TermsCode){
                    
                    vm.defaultList[i].IsActivePublished = false;
                    
                }
            }
        }
    }
})();
(function () {
    'use strict';
    angular.module('paperless').controller('AboutController', AboutController);
    /** @ngInject */
    function AboutController($timeout, SettingSvc, SiteSvc, http, $location,  AboutSvc) {
        var vm = this;
        vm.additem = false;
        vm.defaultList = [];
        getAbout();

        function getAbout() {
            vm.isloading = true;
            http.get('site/' + SiteSvc.CurrentItem.ClientId + '/about').then(function (response) {
                $timeout(function () {
                    update(response.data);
                });
            }, function (error) {
                vm.isloading = false;
            });
        }
        vm.edit = function (item) {
            AboutSvc.currentItem = item;
            $location.path('/configuration/about/add');
        }
        vm.publish = function (item) {
            vm.isloading = true;
            var tmpObj = {};
            tmpObj.AboutInfoCode = item.AboutInfoCode;
            tmpObj.IsPublished = true;
            http.put('site/' + SiteSvc.CurrentItem.ClientId + '/about/published', tmpObj).then(function (response) {
                item.IsActivePublished = true;
                updateStatus(item);
                vm.isloading = false;
            }, function (error) {
                vm.isloading = false;
            });
        }

        function updateStatus(item) {
            for (var i = 0; i < vm.defaultList.length; i++) {
                if (vm.defaultList[i].AboutInfoCode !== item.AboutInfoCode) {
                    vm.defaultList[i].IsActivePublished = false;
                }
            }
        }

        function update(data) {
            vm.defaultList = data;
            vm.isloading = false;
        }
    }
})();
