(function() {
  'use strict';

  angular
    .module('paperless')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($routeProvider, $locationProvider) {
        $routeProvider.when('/dashboard', {
            templateUrl: 'app/dashboard/dashboard.html'
            , controller: 'DashboardController'
            , controllerAs: 'dashboard'
        }).when('/cards', {
            templateUrl: 'app/management/cards/cards.html'
            , controller: 'CardsController'
            , controllerAs: 'cards'
        }).when('/menu', {
            templateUrl: 'app/management/cards/add/menu.add.html'
            , controller: 'MenuController'
            , controllerAs: 'menu'
        }).when('/store', {
            templateUrl:'app/modules/stores/store.html'
            , controller: 'StoreController'
            , controllerAs: 'store'
        }).when('/access', {
            templateUrl:'app/management/access/access.html'
            , controller: 'AccessController'
            , controllerAs: 'access'
        }).when('/loyalty', {
            templateUrl:'app/management/loyalty/loyalty.html'
            , controller: 'LoyaltyController'
            , controllerAs: 'loyalty'
        }).when('/orders', {
            templateUrl:'app/management/orders/orders.html'
            , controller: 'OrdersController'
            , controllerAs: 'orders'
        }).when('/customer', {
            templateUrl:'app/management/customer/customer.html'
            , controller: 'CustomerController'
            , controllerAs: 'customer'
        }).when('/customer/:id', {
            templateUrl:'app/management/customer/detail/customer_detail.html'
            , controller: 'CustomerDetailController'
            , controllerAs: 'customer_detail'
        }).otherwise({
            redirectTo: '/dashboard'
        });
        $locationProvider.html5Mode({
            enabled: true
            , requireBase: false
        });
    }

})();

