(function() {
  'use strict';

  angular
    .module('paperless')
    .config(config);

    /** @ngInject */
   function config($logProvider,  $httpProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

      
        //initialize get if not there
    if (!$httpProvider.defaults.headers.get) {
        $httpProvider.defaults.headers.get = {};    
    }    

    //disable IE ajax request caching
    $httpProvider.defaults.headers.get['If-Modified-Since'] = '0';

    $httpProvider.defaults.headers.get['Cache-Control'] = 'no-cache';
    $httpProvider.defaults.headers.get['Pragma'] = 'no-cache';
      
    $httpProvider.interceptors.push(function () {
                return {
                    'response': function (response) {
                        /*
                        if (response != undefined && response != null &&
                            response.data != undefined && response.data != null && response.data.error != undefined && response.data.error != null) {
                            if (response.data.error.indexOf('Session Timeout') != -1) {
                                alert('Either you are not logged in or your session has expired. Please login to continue.');
                                window.location = './login/login.html';
                            } else if (response.data.error.indexOf('Access Denied') != -1) {
                                alert('Access denied for the operation');
                            }
                        }*/
                        return response;
                    },
                    'responseError':function (response) {
                               
                        if (response != undefined && response != null &&
                            response.data != undefined && response.data != null && response.data.Message != undefined && response.data.Message != null) {
                            if (response.data.Message.indexOf('Authorization has been denied for this request.') != -1) {
                                
                                alert('Either you are not logged in or your session has expired. Please login to continue.');
                                
                                 mgr.signoutRedirect().then(null, function (e) {
                                        console.log(e);
                                   
                                  });
                            }
                        }
                    }
                };
    });
  }

})();

